.glow {
  text-shadow: 0 0 16px rgba(51, 51, 255, 0.25), 0 0 8px rgba(51, 51, 255, 0.25), 0 4px 32px rgba(51, 51, 255, 0.5);
}

.ready {
  color: #00CCCC;
}

.list {
  list-style-type: none;
  padding-left: 0;
  display: grid;
  grid-template-rows: repeat(10, auto);
  grid-auto-flow: column;
  grid-column-gap: 8px;
}